import { useState, useRef } from 'react';

/**
 * @param {number} initialValue - Gi tr khi to (mc nh 5).
 * @param {number} min - Gi tr ti thiu (mc nh 0).
 * @param {number} max - Gi tr ti a (mc nh 100).
 * @param {number} step - Bc nhy khi tng/gim (mc nh 1).
 * @param {number} intervalDelay - Thi gian interval (mc nh 150ms).
 * @returns {object} - Tr v cc hm v gi tr: value, setValue, changeValue, handleMouseDown, handleMouseUp.
 */
export const useCounter = (initialValue = 0, limit, step = 1, intervalDelay = 150) => {
  const [value, setValue] = useState(initialValue);
  const intervalRef = useRef(null);
  const [min, max] = limit;

  // Hm thay i gi tr theo kiu "increase" hoc "decrease"
  const changeValue = (type) => {
    setValue((prevValue) => {
      const newValue = type === "increase" ? prevValue + step : prevValue - step;
      return Math.max(min, Math.min(max, newValue));
    });
  };

  const handleMouseDown = (type) => {
    changeValue(type);
    intervalRef.current = setInterval(() => {
      changeValue(type);
    }, intervalDelay);
  };

  const handleMouseUp = () => {
    clearInterval(intervalRef.current);
    intervalRef.current = null;
  };

  return { value, handleMouseDown, handleMouseUp };
};
