import { toast } from 'react-hot-toast';

/**
 * @param {Event} e - Event tkhi nhp Input.
 * @param {number} index - Th t phn t input nhp.
 * @param {number} JointInput - Bin lu tr d liu input.
 * @param {Function} - Ham thay i bin lu tr.
 * @param {Array} limit - Min-max cua Input.
 */

export const handleInputChange = (e, index, JointInput, setJointInput, limit) => {
    const regex = /^[+-]?\d*\.?\d{0,2}$/;
    const value = e.target.value;
    const cursorPosition = e.target.selectionStart;
    
    // Nu gi tr khng khp vi regex, khng cp nht v t li con tr.
    if (!regex.test(value)) {
        requestAnimationFrame(() => {
            e.target.selectionStart = cursorPosition - 1;
            e.target.selectionEnd = cursorPosition - 1;
        });
        return;
    }

    // Chuyn i chui sang s
    let numericValue = parseFloat(value);
    const [min, max] = limit;

    // Nu gi tr s khng hp l (vt ngoi gii hn), khng cp nht state
    if (!isNaN(numericValue) && (numericValue < min || numericValue > max)) {
        toast.error(`Gi tr phi nm trong khong t ${min} n ${max}`,{
            style: { background: '#ebc8c9', color: '#000'}
        });
        return;
    }

    // Nu hp l, cp nht state mt ln
    const newJointInput = [...JointInput];
    newJointInput[index].input = value;
    setJointInput(newJointInput);
};